<template>
  <div>
    <v-card tile elevation="0" color="#333333" height="725" class="mt-n5">
      <v-container class="pt-16">
        <v-row justify="center" class="mt-16">
          <v-col>
            <v-row justify="center">
              <qrcode-stream
                id="scanner"
                class="my-5 pa-2"
                @decode="onDecode"
                @init="onInit"
              ></qrcode-stream>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>

<script>
export default {
  methods: {
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
    async onDecode(decodedString) {
      this.$store.commit("loading", true);
      let sn = decodedString.substr(3);
      // const url = "http://203.151.164.229:8081/api/v1/atk/devices";
      const url = "https://check-sn.herokuapp.com/api/v1/check_sn_db";
      // const headers = {
      //   Authorization:
      //     "$2a$12$PpO0HqBajBz9YSAiSG.eOutmsIPC45nEm4VsXGCnq4JmqZ4BNUZSy",
      // };
      await this.axios
        .get(url + "/" + sn)
        .then((response) => {
          this.$store.commit("loading", false);
          if (response.data.type) {
            this.$emit("responseStatus", true);
            this.$emit("qrstr", sn);
            this.$emit("result", response.data);
            this.$emit("close");
          }
        })
        .catch((err) => {
          this.$store.commit("loading", false);
          console.log(err);
          this.$emit("responseStatus", false);
        });
    },
  },
};
</script>

<style scoped>
#scanner {
  height: 300px;
  width: 300px;
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='white' stroke-width='16' stroke-dasharray='150' stroke-dashoffset='75' stroke-linecap='square'/%3e%3c/svg%3e");
}
</style>
