import Vue from "vue";
import VueRouter from "vue-router";
import store from "../store";
import main from "../views/main.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "main",
    component: main,
  },
];

const router = new VueRouter({
  mode: "history",
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.name) {
    store.commit("loading", true);
    next();
  }
  next();
});

export default router;
