import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loader: false,
    error: false,
    backBtn: false,
    cancel_scan: false,
  },
  mutations: {
    loading(state, value) {
      state.loader = value;
    },
    check_error(state, value) {
      state.error = value;
    },
    trick_btn(state, value) {
      state.backBtn = value;
    },
    cancelScan(state, value) {
      state.cancel_scan = value;
    },
  },
  actions: {},
  modules: {},
});
