<script>
import loader from "@/components/loader";
import SweetalertIcon from "vue-sweetalert-icons";
import qrscan from "@/components/QRscanner";
export default {
  components: {
    loader,
    SweetalertIcon,
    qrscan,
  },
  data() {
    return {
      tab: 0,
      selected_merchant: "",
      whenshow: "noscan",
      notiCard: false,
      err_handle: false,
      err_msg: 0,
      sn: "",
      oneID: null,
      merchant_data: null,
      step: "first",
      btn_status: false,
    };
  },
  async mounted() {
    var paramsString = window.location.href;
    var url = new URL(paramsString);
    const one_token = url.searchParams.get("onechat_token");
    localStorage.setItem("token", one_token);
    await this.get_onedata();
    this.$store.commit("loading", false);
  },
  computed: {
    btn() {
      return this.btn_status;
    },
    cancel_status() {
      return this.$store.state.cancel_scan;
    },
  },
  watch: {
    sn(newValue) {
      if (newValue.length >= 10) {
        this.btn_status = true;
      } else {
        this.btn_status = false;
      }
    },
    cancel_status(newValue) {
      if (newValue) {
        this.whenshow = "noscan";
        this.$store.commit("trick_btn", false);
        this.$store.commit("cancelScan", false);
      }
    },
  },
  methods: {
    openScan() {
      this.whenshow = "scanning";
      this.$store.commit("trick_btn", true);
    },
    check_error(value) {
      if (!value) {
        this.err_handle = true;
        this.err_msg = 1;
        this.show_notiCard();
      }
    },
    set_sn(result) {
      this.sn = result;
    },
    closeScan() {
      this.whenshow = "noscan";
      this.step = "second";
    },
    async get_onedata() {
      const body = {
        source: localStorage.getItem("token"),
      };
      const headers = {
        Authorization:
          "Bearer A4c106a02df725292bdd6559178ca5e88d155e516115240368068c3ffd2149e48d2cb338c47a54d708bbc7536415a4fd1",
      };

      this.axios
        .post(
          "https://chat-api.one.th/manage/api/v1/login_onechat_token",
          body,
          { headers }
        )
        .then((result) => {
          this.oneID = result.data.account.profile.one_id;
        });
    },
    async regist(merchant) {
      this.$store.commit("loading", true);
      const url = "https://uat-atkscreening.one.th/gov/api/v1/dashboard/user";
      const body = {
        user_id: this.oneID,
        serial_number: this.sn,
      };
      await this.axios
        .put(url, body, { params: { id: merchant } })
        .then((result) => {
          if (result.data.status === "success") {
            this.$store.commit("loading", false);
            this.show_notiCard();
            this.step = "first";
            this.sn = "";
            this.selected_merchant = "";
          }
        })
        .catch((err) => {
          this.$store.commit("loading", false);
          console.log(err);
          this.err_handle = true;
        });
    },
    async get_merchant() {
      const url = "https://uat-atkscreening.one.th/gov/api/v1/dashboard/user";
      await this.axios
        .get(url, { params: { user_id: this.oneID } })
        .then(async (result) => {
          this.merchant_data = result.data.data;
          this.step = "second";
        })
        .catch((err) => {
          this.$store.commit("loading", false);
          console.log(err);
          this.err_handle = true;
        });
    },
    async check_sn_available() {
      this.$store.commit("loading", true);
      // const url = "http://203.151.164.229:8081/api/v1/atk/devices";
      const url = "https://check-sn.herokuapp.com/api/v1/check_sn_db";
      // const headers = {
      //   Authorization:
      //     "$2a$12$PpO0HqBajBz9YSAiSG.eOutmsIPC45nEm4VsXGCnq4JmqZ4BNUZSy",
      // };
      await this.axios
        .get(url + "/" + this.sn)
        .then(async (result) => {
          if (result.data.type) {
            await this.get_merchant();
            this.$store.commit("loading", false);
          }
        })
        .catch(async (err) => {
          this.err_handle = true;
          this.err_msg = 1;
          this.show_notiCard();
          console.log(err);
          this.$store.commit("loading", false);
        });
    },
    show_notiCard() {
      this.notiCard = true;
      setTimeout(() => {
        this.notiCard = false;
        this.err_handle = false;
        this.err_msg = 0;
      }, 2500);
    },
  },
};
</script>

